let pageList = {
	[0]:"/pages/book-category/book-category",
	[1]:"/pages/book-rank/book-rank",
	[2]:"/pages/book-recommend/book-recommend",
	[3]:"/pages/book-booklist/book-booklist",
};
export default {
	props: {
		iconList: {
			type: Array,
			default: () => []
		},
	},
	methods: {
		switchToPage(pageIndex) {
			uni.navigateTo({
				url: pageList[pageIndex],
			});
		}
	},
}
